.bpm-flowchart-arrow-panel{width:218px;background:#fff;box-shadow:0px 0px 8px 0px rgba(221,221,221,0.5);border-radius:2px;position:fixed;padding:4px 4px}.bpm-flowchart-arrow-panel-list{display:flex;flex-flow:row wrap}.bpm-flowchart-arrow-panel-item{margin:5px;display:flex;width:60px;flex-flow:column nowrap;align-items:center;justify-content:center;font-size:12px;white-space:nowrap;height:40px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.bpm-flowchart-arrow-panel-item img{max-height:20px}.bpm-flowchart-arrow-panel-item:hover{background:#f6f6f6}
