.bpm-flowchart-toolbar{display:flex;align-items:center}.bpm-flowchart-toolbar-item{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:flex;align-items:center;justify-content:center;flex-flow:column nowrap;box-sizing:border-box;border-radius:2px;padding:2px 4px;min-width:36px;height:40px;cursor:pointer;margin:0 5px;font-size:12px;color:#666666;white-space:nowrap}.bpm-flowchart-toolbar-item img{width:20px;height:20px}.bpm-flowchart-toolbar-item:hover{background-color:#f5f5f5}.bpm-flowchart-toolbar-item:active{background-color:#eeeeee}
