.bpm-flowchart-menubar{display:flex;align-items:center}.bpm-flowchart-menubar-item{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:flex;align-items:center;justify-content:center;flex-flow:column nowrap;border-radius:2px;min-width:36px;padding:2px 4px;box-sizing:border-box;height:40px;cursor:pointer;margin:0 5px;font-size:12px;color:#666666;white-space:nowrap}.bpm-flowchart-menubar-item .bpm-flowchart-svg{font-size:0}.bpm-flowchart-menubar-item:hover{background-color:#f5f5f5}.bpm-flowchart-menubar-item:active{background-color:#eeeeee}.bpm-flowchart-menubar-sub{display:block}.bpm-flowchart-menubar-sub .bpm-flowchart-svg-title{flex-flow:row nowrap}.bpm-flowchart-menubar-sub .bpm-flowchart-svg-title span{transition:all 0.2s}.bpm-flowchart-menubar-sub .bpm-flowchart-svg{margin:0;margin-right:8px}.bpm-flowchart-menubar-sub>.bpm-flowchart-menubar-item{align-items:flex-start;margin:0;padding:4px 8px}.bpm-flowchart-menubar-sub>.bpm-flowchart-menubar-item:hover .bpm-flowchart-svg-title>span{color:#4285f4 !important}
